<script>
import EducationSection from '@/components/cv_components/EducationSection.vue'
import WorkSection from '@/components/cv_components/WorkSection.vue'
import ProjectSection from '@/components/cv_components/ProjectSection.vue'
import PersonalInfo from '@/components/cv_components/PersonalInfo.vue'
import SummarySection from '@/components/cv_components/SummarySection.vue'

import metadataInstance from '@/models/metadata_model.js' // ËøôË°åÊòØ‰Ω†ÈúÄË¶ÅÊñ∞Â¢ûÁöÑÂØºÂÖ•

export default {
  name: 'ResumeForm',
  components: {
    EducationSection,
    WorkSection,
    ProjectSection,
    PersonalInfo,
    SummarySection
  },
  data() {
    return {
      basicInfo: {
        name: 'Tim',
        phone: '13800000000',
        email: 'tim@example.com'
      },
      educationList: [
        {
          school: 'Ê∏ÖÂçéÂ§ßÂ≠¶',
          time: '2018.09 - 2022.06',
          major: 'ËΩØ‰ª∂Â∑•Á®ã',
          degree: 'Êú¨Áßë',
          gpa: '3.9',
          city: 'Âåó‰∫¨'
        }
      ],
      workList: [
        {
          company: 'ÈòøÈáåÂ∑¥Â∑¥',
          time: '2020.07 - 2023.01',
          title: 'ÂÖ®Ê†àÂ∑•Á®ãÂ∏à',
          city: 'Âåó‰∫¨'
        }
      ],
      projectList: [
        {
          projectName: 'Êô∫ËÉΩÊé®ËçêÁ≥ªÁªü',
          time: '2021.02 - 2021.04',
          role: 'È°πÁõÆÁªèÁêÜ'
        }
      ],
      personalSummary: '', // Â¶ÇÊûúÊúâ‰∏™‰∫∫ÊÄªÁªìÔºåÂèØ‰ª•Âú®Ë°®Âçï‰∏≠Ê∑ªÂä†Áõ∏Â∫îÁöÑËæìÂÖ•È°π
      selectedModule: {
        type: '',
        title: ''
      }
    }
  },
  computed: {
    // Êò†Â∞ÑÊïôËÇ≤ÁªèÂéÜÊï∞ÊçÆ
    mappedEducationList() {
      return this.educationList.map(edu => {
        const [from_time, to_time] = edu.time.split(' - ')
        return {
          title: edu.school,
          content: {
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            content: [
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
            ]
          }
        }
      })
    },
    // Êò†Â∞ÑÂ∑•‰ΩúÁªèÂéÜÊï∞ÊçÆ
    mappedWorkList() {
      return this.workList.map(work => {
        const [from_time, to_time] = work.time.split(' - ')
        return {
          title: work.company,
          content: {
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            content: [
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
            ]
          }
        }
      })
    },
    // Êò†Â∞ÑÈ°πÁõÆÁªèÂéÜÊï∞ÊçÆ
    mappedProjectList() {
      return this.projectList.map(proj => {
        const [from_time, to_time] = proj.time.split(' - ')
        return {
          title: proj.projectName,
          content: {
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            content: [
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
              {
                "bullet_point": "xxxx",
                "content": "xxxx"
              },
            ]
          }
        }
      })
    }
  },
  methods: {
    // ========= Êèê‰∫§ÊåâÈíÆÂ§ÑÁêÜ =========
    handleSubmit() {
      // 1. ÈááÈõÜË°®ÂçïÊï∞ÊçÆ
      const { name, phone, email } = this.basicInfo
      const educationList = this.educationList
      const workList = this.workList
      const projectList = this.projectList

      // 2. ÂÜôÂÖ• metadata_model
      // ---- 2.1 Â°´ÂÜô personalInfo ----
      metadataInstance.setContentForType('personalInfo', {
        name,
        phone,
        email
      })

      // ---- 2.2 Â°´ÂÜô education(educationList) ----
      educationList.forEach((edu) => {
        // ÂÅáËÆæ time ÊòØ "2016.09 - 2020.06" Ê†ºÂºè
        const [fromTime, toTime] = edu.time.split(' - ')
        const title = `${edu.school}`
        metadataInstance.setContentForType(
          'education',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            // ÂàùÂßãÂåñ‰∏Ä‰∏™Á©∫Êï∞ÁªÑÊàñÂç†‰ΩçÂÜÖÂÆπÔºåÂêéÁª≠ÂèØÁî± AI ËøõË°å‰∫ÆÁÇπÊÄªÁªì
            content: []
          },
          title // ÂêåÊ≠•‰Ωú‰∏∫titleÁ¥¢Âºï
        )
      })

      // ---- 2.3 Â°´ÂÜô workExperience(workList) ----
      workList.forEach((work) => {
        const [fromTime, toTime] = work.time.split(' - ')
        const title = `${work.company}`
        metadataInstance.setContentForType(
          'workExperience',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            content: []
          },
          title
        )
      })

      // ---- 2.4 Â°´ÂÜô projectExperience(projectList) ----
      projectList.forEach((proj) => {
        const [fromTime, toTime] = proj.time.split(' - ')
        const title = `${proj.projectName}`
        metadataInstance.setContentForType(
          'projectExperience',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            content: []
          },
          title
        )
      })

      // 3. Â∞ÜÂéüÂßãË°®ÂçïÊï∞ÊçÆÔºàÈùû metadata_model ÁªìÊûÑÔºâÁªßÁª≠Â≠òÂÖ• localStorageÔºåÊñπ‰æøÂêéÁª≠‰ΩøÁî®
      const formData = {
        name,
        phone,
        email,
        educationList,
        workList,
        projectList,
        personalSummary: this.personalSummary
      }
      localStorage.setItem('resumeFormData', JSON.stringify(formData))

      // 4. Ë∑≥ËΩ¨Âà∞ÂàõÂª∫ÁÆÄÂéÜÁöÑÈ°µÈù¢
      this.$router.push('/create-resume')
    },

    // ================== ÊïôËÇ≤ÁªèÂéÜ ==================
    addEducationExperience() {
      this.educationList.push({
        school: '',
        time: '',
        major: '',
        degree: '',
        gpa: '',
        city: ''
      })
    },

    // ================== Â∑•‰ΩúÁªèÂéÜ ==================
    addWorkExperience() {
      this.workList.push({
        company: '',
        time: '',
        title: '',
        city: ''
      })
    },

    // ================== È°πÁõÆÁªèÂéÜ ==================
    addProjectExperience() {
      this.projectList.push({
        projectName: '',
        time: '',
        role: ''
      })
    },

    // ================== Âà†Èô§Âç°Áâá ==================
    removeCard(listName, index) {
      this[listName].splice(index, 1)
    },

    // ================== ÁÇπÂáªÂç°ÁâáÂ§ÑÁêÜ ==================
    handleCardClick(type, name) {
      console.log(`Currently editing ${type}: ${name}`)
      this.selectedModule = { type, title: name }
    },

    // Âä®ÊÄÅËé∑ÂèñÁªÑ‰ª∂
    getComponent(type) {
      switch (type) {
        case 'educationExperience':
          return 'EducationSection'
        case 'workExperience':
          return 'WorkSection'
        case 'projectExperience':
          return 'ProjectSection'
        case 'personalInfo':
          return 'PersonalInfo'
        case 'personalSummary':
          return 'SummarySection'
        default:
          return null
      }
    }
  }
}
</script>

